import React from "react";
import { fetchPopular, fetchTrending } from "@/lib/fetchData";

export default async function Home() {
  const trending = await fetchTrending();
  const popular = await fetchPopular();

  return (
    <main className="min-h-screen bg-slate-50 py-8">
      <div className="container mx-auto max-w-screen-xl px-4">
        <MovieSection title="Trending this week" items={trending} />

        <MovieSection title="Popular" items={popular} />
      </div>
    </main>
  );
}

/*
  Section component: small, reusable block so layout logic is kept in one place.
  - `items` expected to be an array of movie objects
  - falls back to a simple message if empty
*/
